import React, { useEffect, createContext, ReactNode, useState, useContext, Dispatch } from "react";
import Web3 from "web3";
import PixelsMetaverseContract from "../../contracts/hardhat_contracts.json";
import { Contract } from 'web3-eth-contract';
import { AbiItem } from 'web3-utils';
import { ethers } from "ethers"

//const config = require("../../../packages/react-app/converted.config.js")

export const PixelsMetaverseContext = createContext(
  {} as {
    networkId?: number
    contract?: Contract;
    setNetworkId: Dispatch<React.SetStateAction<number | undefined>>;
    setContract: Dispatch<React.SetStateAction<Contract | undefined>>;
    etherContract?: ethers.Contract;
    setEtherContract: Dispatch<React.SetStateAction<ethers.Contract | undefined>>;
  },
);

export const usePixelsMetaverse = () => useContext(PixelsMetaverseContext);

export const supperChain = {
  1: "mainnet",
  3: "ropsten",
  4: "rinkeby",
  5: "goerli",
  42: "kovan",
  100: "xdai",
  137: "polygon",
  1337: "ganache",
  31337: "localhost"
} as { [key in number]: string }

const defaultChain = 31337

type TLibrary = Web3 | ethers.providers.JsonRpcProvider | ethers.providers.Web3Provider | ethers.providers.InfuraProvider | undefined

export const PixelsMetaverseContextProvider = ({ children, library }: { children: ReactNode, library: Web3 | ethers.providers.JsonRpcProvider | ethers.providers.Web3Provider | ethers.providers.InfuraProvider | undefined }) => {
  const [networkId, setNetworkId] = useState<number>();
  const [contract, setContract] = useState<Contract>();
  const [etherContract, setEtherContract] = useState<ethers.Contract>();

  const getContract = async (library: TLibrary, chainId: number) => {
    let deployedNetwork = (PixelsMetaverseContract as any)[chainId || defaultChain] as any
    /* let contractInfo = deployedNetwork[supperChain[chainId]]?.contracts["PixelsMetaverse"];
    if (contract?.options?.address === contractInfo?.address) return
    try {
      if (contractInfo && contractInfo.address) {
        if (library instanceof Web3) {
          const contract = new library.eth.Contract(
            contractInfo && contractInfo.abi as AbiItem[],
            contractInfo && contractInfo.address
          );
          setContract(contract)
        } else {
          let contract2 = new ethers.Contract(
            contractInfo.address,
            contractInfo && contractInfo.abi as AbiItem[],
            library as any
          );
          if (library?.getSigner) {
            const singer = await library?.getSigner()
            contract2 = contract2.connect(singer as any);
          }
          setEtherContract(contract2)
        }
      }
    } catch {
      setContract(undefined)
    } */
  }

  const subscribeProvider = async (provider: any) => {
    if (!provider?.on) return;
    provider.on("close", () => { });
    provider.on("chainChanged", async (chainId: string) => {
      setNetworkId(parseInt(chainId))
    });
  };

  useEffect(() => {
    if (!library) return
    if (library instanceof Web3 && library?.currentProvider) {
      subscribeProvider(library?.currentProvider)
    }

    if (library instanceof Web3) {
      library?.eth.getChainId().then(chainId => {
        setNetworkId(parseInt(`${chainId || 3}`))
      })
    } else {
      library?.getNetwork().then(net => {
        const chainId = net?.chainId;
        setNetworkId(parseInt(`${chainId}`))
      })
    }
  }, [library])

  useEffect(() => {
    if (!networkId || !library) return
    getContract(library, networkId)
  }, [networkId, library])

  return (
    <PixelsMetaverseContext.Provider value={{ networkId, contract, setNetworkId, setContract, etherContract, setEtherContract }}>
      {children}
    </PixelsMetaverseContext.Provider>
  )
}