import React, { DetailedHTMLProps, RefObject, useEffect, useMemo, useRef, useState } from "react"
import { cloneDeep, Dictionary, filter, isArray } from "lodash";
import { useClearCanvas, useConvertedPostion, useDisplayGrad, useDrawImgColor, useGetPositionStr, useSetCanvasSize } from "./hook/canvas";
import { usePixelsMetaverse } from "./PixelsMetaversProvider";
import { get16Color } from "./utilities";
import { gradToSort, positionToGrad } from "./utilities/position";
import { usePixelsMetaverseHandleImg } from "./PixelsMetaverseHandleImgProvider";

export type TMaterialData = {
  id: number,
  owner: string,
  name: string,
  category: string,
  data: string,
  price: number,
  isSale: boolean,
  isOutfit: boolean,
  bgColor: string
}

export type IPosition = { x: number; y: number };

export type IPositionGroup = IPosition[];

export type TPostions = Dictionary<string>

export interface IImgSize {
  width: number;
  height: number
}

export interface IConfigOptions {
  sizeGrid: number,
  withGrid: boolean,
  imgSize: IImgSize,
  bgImg: CanvasImageSource | any,
  bgColor: string,
  bgImgUp: CanvasImageSource | any, // img
  isHandDraw?: boolean,
  gridColor?: string,
  drawColor?: string
}

export type TCanvasHTMLAttributes = DetailedHTMLProps<React.CanvasHTMLAttributes<HTMLCanvasElement>, HTMLCanvasElement>

export type TData = {
  positions?: any;
  materialData?: TMaterialData[]
  size?: string;
  bgColor?: string;
  gridColor?: string
}

export type IPixelsMetaverseImgByAddress = {
  address: string;
  size: number;
  showGrid?: boolean
} & TCanvasHTMLAttributes

export type TPostionsData = {
  positions?: any;
  materialData?: TMaterialData[]
  bgColor?: string;
  gridColor?: string;
  id: any;
  price: any;
  start: any;
  end: any;
  zIndex: any;
  isSale: any;
  isOutfit: any;
  owner: any;
  data: any;
  baseInfo: any
}

export type IPixelsMetaverseImgByPositionData = {
  data: TData;
  size: number; //
  showGrid?: boolean;
  handDraw?: boolean
} & TCanvasHTMLAttributes

export type TSize = number | string

export type IPixelsMetaverseImgByPositionData2 = {
  data: TPostionsData | TPostionsData[];
  size?: TSize | [TSize, TSize]; //[width, height]
  gridColor?: string;
  bgColor?: string
} & TCanvasHTMLAttributes

// 
export const PixelsMetaverseImgByAddress = ({ address, size, showGrid, ...props }: IPixelsMetaverseImgByAddress) => {
  const { networkId, contract, } = usePixelsMetaverse()
  const [materialList, setMaterialList] = useState<any[]>([]);

  useEffect(() => {
    if (!contract) return
    //fetchGetMaterialIdList({ contract, networkId }, { setValue: setMaterialList })
    console.log(networkId, setMaterialList)
  }, [contract])

  const avatarList: TMaterialData[] = useMemo(() => {
    return filter(materialList, item => item?.owner === address && item?.isOutfit)
  }, [materialList])

  return (
    <PixelsMetaverseImgByPositionData
      showGrid={showGrid}
      size={size}
      data={{ materialData: avatarList }}
      {...props}
    />
  )
}

// canvascanvas 
export const PixelsMetaverseImgByPositionData = ({ data, size, showGrid, ...props }: IPixelsMetaverseImgByPositionData) => {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const getPositionData = useConvertedPostion()

  const config = useMemo(() => {
    return {
      imgSize: { width: size, height: size },
      sizeGrid: data.size === "large" ? size / 48 : size / 24,
      withGrid: !!showGrid,
      bgImg: "",
      bgColor: "red" || "#638496",
      gridColor: "red" || "white"
    } as IConfigOptions
  }, [data])

  const getPositionStr = useGetPositionStr(data?.materialData || [])

  const postionData = useMemo(() => {

    if (!getPositionStr) return {}
    if (!getPositionStr?.includes("-")) return {}

    return getPositionData({ ...data, positions: getPositionStr })
  }, [getPositionStr])

  return (
    <PixelsMetaverseCanvas
      config={config}
      data={postionData}
      canvasRef={canvasRef}
      showGridColor
      style={{
        backgroundColor: config?.bgColor
      }}
      {...props}
    />
  )
}

export const PixelsMetaverseImgByPositionData2 = ({ data, size, gridColor, bgColor, ...props }: IPixelsMetaverseImgByPositionData2) => {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const getPositionData = useConvertedPostion()
  const ref = useRef<HTMLDivElement>(null)
  const list = isArray(data) ? data : [data]
  const [{ width, height }, setSize] = useState({ width: 0, height: 0 })

  const config = useMemo(() => {
    return {
      imgSize: { width, height },
      sizeGrid: 24,
      withGrid: !!gridColor,
      bgColor: gridColor || "#638496",
      gridColor: bgColor || "white"
    } as IConfigOptions
  }, [list, width, height])

  const getPositionStr = useGetPositionStr(isArray(data) ? data : [data])

  const postionData = useMemo(() => {

    if (!getPositionStr) return {}
    if (!getPositionStr?.includes("-")) return {}

    return getPositionData({ ...list, positions: getPositionStr })
  }, [getPositionStr])

  useEffect(() => {
    if (!ref?.current) return
    setSize({ width: ref?.current?.offsetWidth, height: ref?.current?.offsetHeight })
  }, [ref])

  return (
    <div ref={ref} style={{ width: (isArray(size) ? size[0] : size) || "100%", height: (isArray(size) ? size[1] : size) || "100%", border: "1px solid red" }}>
      <PixelsMetaverseCanvas2
        config={config}
        data={postionData}
        canvasRef={canvasRef}
        showGridColor
        style={{
          backgroundColor: config?.bgColor
        }}
        {...props}
      />
    </div>
  )
}

export const PixelsMetaverseHandleImg = ({ canvasRef, showBgImg, showGridColor, ...props }: { canvasRef: React.RefObject<HTMLCanvasElement>, showBgImg?: boolean; showGridColor?: boolean; } & TCanvasHTMLAttributes) => {
  const { config, getGradPosition, setPositionsArr, positionsArr, dealClick: { add, value, remove } } = usePixelsMetaverseHandleImg()
  let timer: any;

  const clickCanvas = (event: MouseEvent, canvasRef: RefObject<HTMLCanvasElement> | null) => {
    clearTimeout(timer);
    if (!canvasRef?.current) return
    timer = setTimeout(() => {
      const position = positionToGrad(getGradPosition(event, canvasRef.current), config.sizeGrid)
      const context = canvasRef.current?.getContext("2d") as CanvasRenderingContext2D
      const { x, y } = getGradPosition(event, canvasRef.current)
      let data = context.getImageData(x, y, 1, 1).data
      const rgba = 'rgb(' + data[0] + ',' + data[1] + ',' + data[2] + ')';
      const sort = gradToSort(position, config.imgSize.width, config.sizeGrid)
      const ponitColor = get16Color(config.drawColor || rgba)
      add(sort, ponitColor)
      if (!positionsArr.includes(sort)) {
        setPositionsArr(((pre) => ([...pre, sort])))
      }
    }, 200);
  }

  const doubleClickCanvas = (event: MouseEvent, canvasRef: RefObject<HTMLCanvasElement> | null) => {
    clearTimeout(timer);
    if (!canvasRef?.current) return
    const position = positionToGrad(getGradPosition(event, canvasRef.current), config.sizeGrid)
    const sort = gradToSort(position, config.imgSize.width, config.sizeGrid)
    remove(sort)
    setPositionsArr((pre) => {
      const positionss = cloneDeep(pre)
      const positionssIndex = positionss.indexOf(sort)
      positionss.splice(positionssIndex, 1)
      return positionss
    })
  }

  return (
    <PixelsMetaverseCanvas
      config={config}
      data={value}
      canvasRef={canvasRef}
      showBgImg={showBgImg}
      showGridColor={showGridColor}
      style={{
        backgroundColor: config?.bgColor
      }}
      onClick={(e) => {
        if (!config.isHandDraw) return
        const event = e as unknown as MouseEvent
        clickCanvas(event, canvasRef)
      }}
      onDoubleClick={(e) => {
        if (!config.isHandDraw) return
        const event = e as unknown as MouseEvent
        doubleClickCanvas(event, canvasRef)
      }}
      {...props}
    />)
}

export const PixelsMetaverseCanvas = ({
  data,
  config,
  canvasRef,
  showBgImg,
  showGridColor,
  ...props
}: {
  data: Dictionary<string>; // 
  config: IConfigOptions;
  showBgImg?: boolean;
  showGridColor?: boolean;
  canvasRef: RefObject<HTMLCanvasElement> | null;
} & TCanvasHTMLAttributes) => {
  const setSize = useSetCanvasSize()
  const setGrad = useDisplayGrad()
  const drawPixelsImg = useDrawImgColor()
  const clearCanvas = useClearCanvas()

  useEffect(() => {
    const canvas = canvasRef?.current
    if (!canvas || config.imgSize.width <= 0 || (config?.withGrid && config.sizeGrid <= 0)) return
    const context = canvas.getContext("2d") as CanvasRenderingContext2D
    setSize(canvas, config.imgSize)
    clearCanvas(canvas, context)
    if (config.imgSize.width > 1 && config.imgSize.height > 1 && config.sizeGrid >= 1) {
      //showBgImg && dealBgImg(canvas, context, config.bgImgUp)
      showGridColor && drawPixelsImg(config.imgSize, context, data, config.sizeGrid)
    }
    if (config.sizeGrid >= 1) {
      setGrad(context, config)
    }
  }, [canvasRef, data, config])

  return (
    <canvas
      ref={canvasRef}
      width={0}
      height={0}
      {...props}
    >Sorry, your browser dose not support canvas.</canvas>
  )
}

