import "./position"
import "./radix"

export const get16Color = (color: string) => {
  // RGB
  const reg = /^(rgb|RGB)/;
  if (reg.test(color)) {
    let strHex = "#";
    // RGB3
    const colorArr = color.replace(/(?:\(|\)|rgb|RGB)*/g, "").split(",");
    // 16
    for (let i = 0; i < colorArr.length; i++) {
      let hex = Number(colorArr[i]).toString(16);
      if (hex.length < 2) {
        hex = `0${hex}`
      }
      strHex += hex;
    }
    return strHex;
  } else {
    return String(color);
  }
}